/* eslint-disable compat/compat */
import React, { Component } from 'react';
import '../styles/App.css';
import {
    SelectItemGroup,
    SelectItem,
    DatePicker,
    DatePickerInput,
    TimePicker,
    TimePickerSelect,
    Button,
} from 'carbon-components-react';
import axios, { post } from 'axios';
import swal from 'sweetalert';
import Loading from './Loading';

let minDate = (new Date().getDate() + 1) + '/' + (new Date().getMonth() + 1) + '/' + new Date().getFullYear();
let maxDate = new Date().getDate() + '/' + (new Date().getMonth() + 2) + '/' + new Date().getFullYear();

const APP_VIEWS = {
    INPUT: 'INPUT',
    LOADING: 'LOADING',
};

const DisplayImage = ({ selectedOption }) => {
    switch (selectedOption) {
        default:
            return <img src="http://chanhassenautoplex.com/wp-content/uploads/2018/01/CarsAndCaves106-2-of-18.jpg" alt="yaris"/>;
        case 'yaris':
            return <img src="https://i.blogs.es/0f9d7d/toyota-yaris-r-2018_9/450_1000.jpg" alt="yaris"/>;
        case 'corolla':
            return <img src="https://www.cstatic-images.com/car-pictures/xl/usc70toc041g021001.png" alt="corolla"/>;
        case 'sienna':
            return <img src="https://www.cstatic-images.com/car-pictures/xl/usc80tov111a021001.png" alt="sienna"/>;
    }
};

class Form extends Component {
    constructor(props) {
        super(props);
        this.state = {
            app_view: APP_VIEWS.INPUT,
            sucs: [],
            cars: [],
            cars_by_suc: [],
        };
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount() {
        const url = 'http://localhost:3001/getAllInfo';
        axios.get(url)
            .then(res => {
                // Expected result
                /* data = {
                    sucs: [],
                    cars: [],
                    cars_by_suc: [],
                } */
                let data = res.data.data;
                this.setState({
                    // response: res.data.message,
                    ...data,
                    isLoading: false,
                });
            })
            .catch(err => {
                // handle error
                console.warn(err);
                this.setState({
                    response: err.message,
                });
            });
        this.callApi()
            .then(res => this.setState({ response: res.express }))
            .catch(err => console.log(err));
    }

    callApi = async () => {
        const response = await fetch('/');
        const body = await response.json();
        if (response.status !== 200) { throw Error(body.message); }
        return body;
    };

    handleChange(event) {
        console.log('Event.target.value: ', event.target.value);
        this.setState({ selectedOption: event.target.value });
    }

    handleSubmit() {

        const url = '/';
        const formData = new FormData();
        formData.append('file', new Blob(this.state.filesToBeSent[0]), 'file.csv');
        const config = {
            timeout: 300000,
            headers: {
                'content-type': 'multipart/form-data',
            },
        };

        this.setState({
            ...this.setState,
            app_view: APP_VIEWS.LOADING,
        });

        post(url, formData, config)
            .then(res => {
                let resStatus = res.data.status;
                let view;
                switch (resStatus) {
                    default:
                        break;
                    case 'system_error':
                        view = APP_VIEWS.INPUT;
                        swal('Whoops', 'There was an error, please try again. If this error persists, please contact the site administrator.', 'error');
                        break;
                    case 'fail':
                        view = APP_VIEWS.INPUT;
                        swal('Whoops', res.data.messages.message, 'error');
                        break;
                    case 'success':
                        view = APP_VIEWS.INPUT;
                        //this is the Excel
                        this.action(res.data.rejected, res.data.data.USFED.DNS, res.data.data.USFED.FIXED, res.data.data.WW.DNS, res.data.data.WW.FIXED);
                        swal('Awesome!', 'Your file has ' + 
                        {
                            buttons: ['Cancel', 'Send!'],
                        })
                            .then((send) => {
                                view = APP_VIEWS.LOADING;
                                this.setState({
                                    ...this.setState,
                                    data: res.data,
                                    app_view: view,
                                });
                                let url = '/send_invite/';
                                let confirm = send ? 'True' : '';
                                axios.post(url, {
                                    WW: { send: confirm }, 
                                    USFED: { send: confirm },
                                })
                                    .then(() => {
                                        view = APP_VIEWS.INPUT;
                                        // handle success
                                        let data = { ...this.state.data };
                                        this.setState({
                                            filesToBeSent: [],
                                            invalidEmail: true,
                                            invalidEmailBool: false,
                                            filesPreview: [],
                                            app_view: view,
                                            data: data,
                                        });
                                        if (confirm === 'True') {
                                            swal(
                                                {
                                                    icon: 'success',
                                                    className: 'swal-final',
                                                }
                                            );
                                        } else {
                                            swal('Your file has been discarded', '',
                                                {
                                                    icon: 'warning',
                                                }
                                            );
                                        }
                                    })
                                    .catch(err => {
                                        view = APP_VIEWS.INPUT;
                                        // handle error
                                        console.log(err);
                                        this.setState({
                                            response: err.message,
                                            app_view: view,
                                        });
                                        swal('Bad bad bad...', '',
                                            {
                                                icon: 'error',
                                            }
                                        );
                                    });
                            });
                        break;
                } // Uno u otro
                this.setState({
                    ...this.setState,
                    data: res.data,
                    app_view: view,
                });
            })
            .catch((err) => {
                let view;
                console.error('error form: ', err);
                view = APP_VIEWS.INPUT;
                this.setState({
                    response: err.message,
                    app_view: view,
                    filesToBeSent: [],
                    invalidEmail: true,
                    filesPreview: [],
                });
                swal('The process is taking too long, please try again later.', '',
                    {
                        icon: 'error',
                    }
                );
            });
    }

    render() {
        switch (this.state.app_view) {
            default:
                return <div></div>;
            case APP_VIEWS.INPUT:
                return (
                    <div className="rentacar-form">
                        <div className="rentacar-form-flex">
                            <div className="rentacar-form-inside">
                                <div className="rentacar-form-column">
                                    <div className="mail-datos-panel">
                                        <p className="form-datos" >
                                            Por favor seleccione una sucursal
                                        </p>
                                        <div className="dropdown-renta">
                                            <div className="bx--form-item">
                                                <select
                                                    //onChange={ anonymous }
                                                    defaultValue="placeholder-item"
                                                    id="select-1"
                                                    className="bx--select-input"
                                                    disabled={ null }
                                                    data-invalid={ null }
                                                >
                                                    <SelectItem
                                                        disabled
                                                        hidden
                                                        value="placeholder-item"
                                                        text="Sucursal"
                                                    />
                                                    <SelectItemGroup label="ZMG">
                                                        {
                                                            /* ARRAY[this.state.suc].forEach(element => {
                                                                <SelectItem value={ element } text={ element } />
                                                            }); */
                                                        }
                                                        <SelectItem value="chapalita" text="Chapalita" />
                                                        <SelectItem value="gdlcentro" text="Guadalajara Centro" />
                                                        <SelectItem value="zapopan" text="Zapopan" />
                                                    </SelectItemGroup>
                                                </select>
                                            </div>
                                        </div>
                                        <p className="form-datos" >
                                            Por favor ingrese su direccion
                                        </p>
                                        <input className="input-renta"></input>
                                        <p className="form-datos" >
                                            Por favor seleccione el estado
                                        </p>
                                        <div className="dropdown-renta">
                                            <div className="bx--form-item">
                                                <select
                                                    //onChange={ anonymous }
                                                    defaultValue="placeholder-item"
                                                    id="select-1"
                                                    className="bx--select-input"
                                                    disabled={ null }
                                                    data-invalid={ null }
                                                >
                                                    <SelectItem
                                                        disabled
                                                        hidden
                                                        value="placeholder-item"
                                                        text="Estado"
                                                    />
                                                    <SelectItemGroup label="Mexico">
                                                        <SelectItem value="aguascalientes" text="Aguascalientes" />
                                                        <SelectItem value="bjn" text="Baja California" />
                                                        <SelectItem value="bjs" text="Baja California Sur" />
                                                        <SelectItem value="campeche" text="Campeche" />
                                                        <SelectItem value="chiapas" text="Chiapas" />
                                                        <SelectItem value="chihuahua" text="Chihuahua" />
                                                        <SelectItem value="coahuila" text="Coahuila de Zaragoza" />
                                                        <SelectItem value="colima" text="Colima" />
                                                        <SelectItem value="durango" text="Durango" />
                                                        <SelectItem value="cdmx" text="Estado de México" />
                                                        <SelectItem value="guanajuato" text="Guanajuato" />
                                                        <SelectItem value="guerrero" text="Guerrero" />
                                                        <SelectItem value="hidalgo" text="Hidalgo" />
                                                        <SelectItem value="jalisco" text="Jalisco" />
                                                        <SelectItem value="michoacan" text="Michoacán de Ocampo" />
                                                        <SelectItem value="morelos" text="Morelos" />
                                                        <SelectItem value="nayarit" text="Nayarit" />
                                                        <SelectItem value="nuevo_leon" text="Nuevo León" />
                                                        <SelectItem value="oaxaca" text="Oaxaca" />
                                                        <SelectItem value="puebla" text="Puebla" />
                                                        <SelectItem value="queretaro" text="Querétaro" />
                                                        <SelectItem value="quintana_roo" text="Quintana Roo" />
                                                        <SelectItem value="slp" text="San Luis Potosí" />
                                                        <SelectItem value="sinaloa" text="Sinaloa" />
                                                        <SelectItem value="sonora" text="Sonora" />
                                                        <SelectItem value="tabasco" text="Tabasco" />
                                                        <SelectItem value="tamaulipas" text="Tamaulipas" />
                                                        <SelectItem value="tlaxcala" text="Tlaxcala" />
                                                        <SelectItem value="veracruz de Ignacio de la Llave" text="Veracruz de Ignacio de la Llave" />
                                                        <SelectItem value="yucatan" text="Yucatán" />
                                                        <SelectItem value="zacatecas" text="Zacatecas" />
                                                    </SelectItemGroup>
                                                </select>
                                            </div>
                                        </div>
                                        <p className="form-datos" >
                                            Por favor seleccione la fecha de inicio y fin
                                        </p>
                                        <div className="fecha-input">
                                            <DatePicker
                                                id="date-picker"
                                                //onChange={ anonymous }
                                                //onClose={ anonymous }
                                                minDate={ minDate }
                                                maxDate={ maxDate }
                                                datePickerType="range"
                                                locale="es"
                                                dateFormat="d/m/Y"
                                            >
                                                <DatePickerInput
                                                    id="date-picker-input-id"
                                                    className="some-class"
                                                    pattern="d{1,2}/d{4}"
                                                    placeholder="dd/mm/yyyy"
                                                    invalidText="Ingresa una fecha"
                                                    //onClick={ anonymous }
                                                    //onChange={ anonymous }
                                                />
                                                <DatePickerInput
                                                    id="date-picker-input-id-2"
                                                    className="some-class"
                                                    pattern="d{1,2}/d{4}"
                                                    placeholder="dd/mm/yyyy"
                                                    invalidText="Ingresa una fecha"
                                                    //onClick={ anonymous }
                                                    //onChange={ anonymous }
                                                />
                                            </DatePicker>
                                        </div>
                                        <p className="form-datos">
                                            Por favor selecciona una hora de entrega y de devolucion
                                        </p>
                                        <div className="hora-input">
                                            <TimePicker
                                                //id="time-picker"
                                                hideLabel={ false }
                                                invalidText="Ingresa una hora"
                                                /* onClick={ anonymous }
                                                onChange={ anonymous }
                                                onBlur={ anonymous } */
                                            >
                                                <TimePickerSelect id="time-picker-select-1" labelText="Selecciona una hora">
                                                    <SelectItem value="AM" text="AM" />
                                                    <SelectItem value="PM" text="PM" />
                                                </TimePickerSelect>
                                            </TimePicker>
                                            <TimePicker
                                                id="time-picker"
                                                hideLabel={ false }
                                                invalidText="Ingresa una hora"
                                                /* onClick={ anonymous }
                                                onChange={ anonymous }
                                                onBlur={ anonymous } */
                                            >
                                                <TimePickerSelect id="time-picker-select-2" labelText="Selecciona una hora">
                                                    <SelectItem value="AM" text="AM" />
                                                    <SelectItem value="PM" text="PM" />
                                                </TimePickerSelect>
                                            </TimePicker>
                                        </div>
                                    </div>
                                </div>
                                <div className="car-model">
                                    <div className="car-name">
                                        <select
                                            onChange={ this.handleChange.bind(this) }
                                            defaultValue="placeholder-item"
                                            id="select-1"
                                            className="bx--select-input"
                                            disabled={ null }
                                            data-invalid={ null }
                                        >
                                            <SelectItem
                                                disabled
                                                hidden
                                                value="placeholder-item"
                                                text="Modelo"
                                            />
                                            <SelectItemGroup label="Chapalita">
                                                <SelectItem value={ 'yaris' } text="Yaris R 2019"/>
                                                <SelectItem value={ 'corolla' } text="Corolla 2019"/>
                                                <SelectItem value={ 'sienna' } text="Sienna 2019"/>
                                            </SelectItemGroup>
                                        </select>
                                    </div>
                                    <div className="car-image">
                                        <DisplayImage selectedOption={ this.state.selectedOption } />
                                    </div>
                                    <div className="submitRentar">
                                        <Button className="submit_button"
                                            kind="tertiary" onClick={ (event) => this.handleSubmit(event) } >
                                                Rentar!
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                );

            case APP_VIEWS.LOADING:
                return (
                    <Loading />
                );
        }
    }
}

export default Form;